* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.App {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    height: 100vh;
    font-family: "Arial", "sans-serif";
}

.input-wrapper {
    display: inline-block;
    position: relative;
}

input {
    padding: 8px;
    font-family: "Arial", "sans-serif";
    font-size: 16px;
}

input[type="text"] {
    width: 100%;
    border-width: 1px;
    background-color: #f5f5f5;
}

input[type="submit"] {
    margin-left: 10px;
    border: none;
    border-radius: 5px;
    padding: 8px 12px;
    background-color: #1982c4;
    color: #f5f5f5;
    cursor: pointer;
}

.autocomplete-container {
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    z-index: 99;
    border: 1px solid black;
    border-bottom: none;
    border-top: none;
}

.option {
    border-bottom: 1px solid #999;
    padding: 8px;
    cursor: pointer;
}

.option:hover {
    background-color: #dfdfdf;
}

